import React from 'react';

const List = ({people}) => {
  return (
    <>
      {people.map((person)=>{
      	const {id,age,name,image} = person;
      	return <article key={id} className="person">
      		<img src={image} alt={name}/>
      		<h4>{name}</h4>
      		<p>{age} years</p>
      	</article>
      })}
    </>
  );
};

export default List;